table {
    border-collapse: collapse;
    width: 100%;
}

th, td {
    text-align: left;
    padding: 8px;
    text-align: left;
}

.info-panel {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 400px;
    background-color: white;
    padding: 10px;
    max-height: viewport;
    overflow: auto;
    border: 1px solid black;
    z-index: 998;
    box-shadow: 2px 2px 5px grey;
  }
  .info-panel-enlarge {
    position: fixed;
    top: 10px;
    right: 10px;
    left: 10px;
    bottom: 10px;

    z-index: 2000;
    background-color: white;
    padding: 10px;
    border: 1px solid black;
    box-shadow: 2px 2px 5px grey;
    z-index: 998;
  }
  
.close-button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 20px;
    height: 20px;
    background-color: transparent;
    color: black;
    text-align: center;
    cursor: pointer;
  }

  .modal {
    position: relative;
    height: "10px";
    display: flex;
    justify-content: right;
    align-items: center;
    z-index: 999;
  }
  
  .enlarge-button {
    background-color: #FFFFFF;
    color: #000000;
    padding: 8px;
    border: none;
    border-radius: 10px;
    font-size: 10px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .enlarge-button:hover {
    background-color: #000000;
    color: #FFFFFF;
  }
  
  
  .enlarged .enlarge-button:hover {
    background-color: #FFFFFF;
    color: #000000;
  }
  
  


.exit-button {
    background-color: #FFFFFF;
    color: #000000;
    padding: 8px;
    border: none;
    border-radius: 10px;
    font-size: 10px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .exit-button:hover {
    background-color: #000000;
    color: #FFFFFF;
  }